export default {
	data() {
		return {
			assetInfo: null,
			canAction: false,
			isMe: false
		}
	},
	onLoad: function(options) {
		getApp().sendSocket("getAssetInfo", {no: options.no})
		getApp().sendSocket("isMe", {no: options.no})
		if (options.action && options.action == "true") {
			this.canAction = true
		}
	},
	onShow: function() {
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "assetInfo") {
				this.assetInfo = obj
			} else if (msg == "isMe") {
				this.isMe = true
			} else if (msg == "borrowSuccess") {
				uni.showToast({
					icon: "success",
					title: "领用成功"
				})
				this.canAction = false
			} else if (msg == "lendSuccess") {
				uni.showToast({
					icon: "success",
					title: "归还成功"
				})
				this.canAction = false
			}
		},
		borrow: function () {
			getApp().sendSocket("assetBorrow", {as_no: this.assetInfo.as_no})
		},
		lend: function () {
			getApp().sendSocket("assetLend", {as_no: this.assetInfo.as_no})
		}
	}
}
